import { Component, OnInit } from '@angular/core';
import { FaqService } from 'src/app/core/services/faq.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';
@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss']
})
export class FaqComponent implements OnInit {

  filterTerm: string = '';
  originalFaqs: any = [];
  faqs: any = [];
  noResultsFound: any = [];
  steps: IBreadcrumb[];

  constructor(
    private titleS: Title,
    private faqService: FaqService,
    private route: ActivatedRoute,
    private router: Router,
    private gtm: GtmService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.titleS.setTitle('Zero Km - FAQ');
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - FAQ');
    this.faqService.getFaqs().subscribe(faqs => {
      this.originalFaqs = faqs;
      this.faqs = faqs;

      this.route.queryParams.subscribe(params => {
        if (params.t) {
          this.filterTerm = params.t;
          this.filterFaq(params.t);
        }
      });
    });

    this.noResultsFound = [{ question: "Nenhum resultado encontrado :(", answer: "Tente mudar seus critérios de busca" }];

    this.steps = [{
      id: '0',
      label: 'Voltar para home',
      link: '/'
    },
    {
      id: '1',
      label: 'Perguntas frequentes'
    }];
  }

  replaceAccentedChars(valor) {
    return valor.replace(/[ÀÁÂÃÄÅàáâãäå]/g, 'A')
      .replace(/[ÈÉÊËèéëê]/g, 'E').replace(/[ÌÍÎÏìíîï]/g, 'I')
      .replace(/[ÒÓÔÕÖòóôõö]/g, 'O').replace(/[ÙÚÛÜùúûü]/g, 'U')
      .replace(/[Çç]/g, 'C').trim();
  }

  filterFaq(term: string) {
    const normalizedTerm = this.replaceAccentedChars(term.toUpperCase());
    const regExpTerm = normalizedTerm.replace(/ /g, '.*') + '.*';
    const regexFaq = new RegExp(regExpTerm);

    this.faqs = this.originalFaqs.filter(x => {
      return x.question.toUpperCase().match(regexFaq) !== null ||
        this.replaceAccentedChars(x.question).toUpperCase().match(regexFaq) !== null ||
        x.answer.toUpperCase().match(regexFaq) !== null ||
        this.replaceAccentedChars(x.answer).toUpperCase().match(regexFaq) !== null;
    });

    if (this.faqs.length === 0) {
      this.faqs = this.noResultsFound;
    }
  }

  getFaqs(event) {
    if (event.target.value) {
      this.filterFaq(event.target.value);

      if (event.keyCode === 13) {
        event.preventDefault();
        event.target.blur();
      }
    }
  }

  checkIsEmpty(event) {
    if (!event.target.value) {
      this.faqs = this.originalFaqs;
    }
  }
}
